.work-content {
  margin-bottom: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  animation: work-content 350ms ease;
}

.slideshow-container {
  box-sizing: border-box;
  position: relative;
  margin: auto;
  border-width: 3px;
  border-style: solid;
  border-image: linear-gradient(to bottom, rgb(15, 194, 248), rgb(240, 15, 184))
    1 100%;
}

.prev,
.next {
  cursor: pointer;
  width: auto;
  transition: 0.6s ease;
  border-radius: 50%;
  user-select: none;
  position: absolute;
  left: 50%;
  bottom: 0;
  height: 36px;
  margin-left: -40px;
  outline: none;
}

.next {
  margin-left: 4px;
}

.prev img,
.next img {
  transition: inherit;
}

.prev:hover,
.prev:focus,
.next:hover,
.next:focus {
  background-color: #f1f1f1;
}

.prev:hover img,
.prev:focus img,
.next:hover img,
.next:focus img {
  filter: invert(100%);
}

.slides {
  display: none;
  width: 100%;
  height: 100%;
  padding: 0 0.75em;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 15px;
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

.slides h3 {
  margin-bottom: 1em;
  margin-top: 0;
  text-align: center;
}

.slides ul {
  margin: 0;
  list-style-image: url('../../assets/round_label_important_white_18dp.png');
}

.slides div {
  text-align: center;
  display: flex;
  justify-content: space-around;
  margin: 1em 0 2em 0;
}

.slides a {
  text-decoration: none;
  color: #71efef;
}

.slides a:hover,
.slides a:focus {
  background-image: -webkit-linear-gradient(
    bottom right,
    rgb(15, 194, 248),
    rgb(240, 15, 184)
  );
  background-image: linear-gradient(
    to bottom right,
    rgb(15, 194, 248),
    rgb(240, 15, 184)
  );
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.title .description {
  font-size: 0.5em;
}

.last-slide ul {
  margin-bottom: 2em;
}

@-webkit-keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}

@keyframes work-content {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media only screen and (max-width: 399px) {
  .slides {
    font-size: 13px;
  }
}

@media only screen and (max-width: 767px) {
  .prev img,
  .next img {
    height: 27px;
    width: 27px;
  }

  .prev,
  .next {
    height: 27px;
  }
}

@media only screen and (min-width: 768px) {
  .slides {
    font-size: 18px;
  }
}

@media only screen and (min-width: 1024px) {
  .slides {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1200px) {
  .slides {
    font-size: 25px;
  }
}
