.email {
  margin: 0 auto auto;
  padding-bottom: 50px;
}

.email a {
  color: black;
  background: white;
  text-decoration: none;
  font-size: 20px;
  padding: 0 10px;
  transition: all 350ms linear;
  display: block;
  animation: email 350ms ease;
}

.email a:hover,
.email a:focus {
  transform: scale(1.1);
  color: white;
  background: linear-gradient(
    to bottom right,
    rgb(15, 194, 248),
    rgb(240, 15, 184)
  );
}

@keyframes email {
  0% {
    opacity: 0;
    transform: translateX(20px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@media only screen and (max-width: 399px) {
  .email {
    margin: 25px 0;
    padding-bottom: 0;
  }
}

@media only screen and (min-width: 600px) {
  .email a {
    font-size: 30px;
  }
}

@media only screen and (min-width: 768px) {
  .email a {
    font-size: 35px;
  }
}

@media only screen and (min-width: 1024px) {
  .email a {
    font-size: 45px;
  }
}

@media only screen and (min-width: 1200px) {
  .email a {
    font-size: 50px;
  }
}
