.app-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  color: white;
  height: 100vh;
  background: linear-gradient(-45deg, #000000, #2e2d2d, #414141);
}

.nav-links-container {
  position: absolute;
  right: 10px;
  bottom: 30px;
}

.nav-link {
  height: 20px;
  text-align: right;
}

.nav-link a {
  text-decoration: none;
  font-size: 15px;
  line-height: 30px;
  color: white;
}

.nav-link a.active {
  background-image: -webkit-linear-gradient(
    bottom right,
    rgb(15, 194, 248),
    rgb(240, 15, 184)
  );
  background-image: linear-gradient(
    to bottom right,
    rgb(15, 194, 248),
    rgb(240, 15, 184)
  );
  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 70%;
  height: 70%;
}

.title {
  font-family: "Raleway", sans-serif;
  margin: 0 0 auto;
  align-self: flex-start;
  letter-spacing: 2px;
  animation: title 350ms linear;
}

@keyframes title {
  0% {
    opacity: 0;
    transform: translateX(-20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@media only screen and (max-width: 399px) {
  .nav-links-container {
    left: 10px;
    top: 10px;
    bottom: unset;
  }

  .title {
    font-size: 30px;
  }
}

@media only screen and (min-width: 400px) {
  .title {
    font-size: 35px;
  }
}

@media only screen and (min-width: 600px) {
  .title {
    font-size: 45px;
  }
}

@media only screen and (min-width: 768px) {
  .title {
    font-size: 50px;
  }

  .nav-link {
    height: 25px;
  }

  .nav-link a {
    font-size: 20px;
  }
}

@media only screen and (min-width: 1024px) {
  .title {
    font-size: 60px;
  }

  .nav-link {
    height: auto;
  }

  .nav-link a {
    font-size: 25px;
  }
}
